.main-footer{
    position: relative;
    width: 100%;
    min-height: 980px;
    background: #ffffff url(../../img/footerBack.png) bottom no-repeat;
}

.contact{
    position: absolute;
    top: 250px;
    right: 20%;
    width: 570px;
}

.submit-form{
    display: flex;
    margin-top: 40px;
    flex-direction: column;
}

.submit-form_item{
    width: 385px;
    margin-bottom: 40px;
    border: none;
    border-bottom: 2px solid #000000;
}

.submit-form_sen-btn{
    background: none;
    border: none;
    cursor: pointer;
    width: 40px;
    margin: 0;
    padding: 0;
    font-family: "Open Sans" ,sans-serif;
    font-size: 20px;
    font-weight: bold;
}

.social-network{
    &::before{
        display: block;
        position: absolute;
        top: 30px;
        left: -130px;
        transform: rotate(90deg);
        content: "";
        background: #000000;
        width: 200px;
        height: 1px;
    }
    display: flex;
    flex-direction: column;
    position: absolute;
    right: 50px;
    top: 250px;
}

.social-network_item{
    margin-top: -40px;
    margin-bottom: 80px;
}